<template>
<v-container fill-height>
    <v-row justify="center" align="center">
<v-btn :loading="loading" @click="unlock" color="primary" block>Заблокировать</v-btn>
    </v-row>
</v-container>
</template>

<script>
export default {
  props: ['ticket'],
  data: () => ({
    loading: false
  }),
  methods: {
    async unlock () {
      this.loading = true
      await this.ticket.lock(this.api).catch(v => alert(v.message))
      this.loading = false
    }
  }
}
</script>
