<template>
<TabContent v-if="ticket && !error">
  <Workshape :ticket="ticket"/>
  <template v-slot:footer>
    <Input v-if="ticket" :ticket="ticket"/>
  </template>
  <template v-slot:leftSidebar>
    <TicketDetailInfo :ticket="ticket"/>
  </template>
  <template v-slot:rightSidebar>
    <TicketInfo :ticket="ticket"/>
  </template>
</TabContent>
<TabContentLoading :error="error" :tab="value" v-else/>
</template>

<script>
import TabContent from '@/render/components/Common/TabContent.vue'
import TabContentLoading from '@/render/components/Common/TabContentLoading.vue'
import Ticket from '@/common/ticket.js'
import Workshape from '@/render/components/Ticket/Workshape.vue'
import Input from '@/render/components/Ticket/Input.vue'
import TicketInfo from '@/render/components/Ticket/TicketInfo.vue'
import TicketDetailInfo from '@/render/components/Ticket/TicketDetailInfo.vue'
// import Title from '@/render/components/Ticket/Title.vue'

export default {
  components: { TabContent, TabContentLoading, Workshape, Input, TicketInfo, TicketDetailInfo /*, TicketDetailInfo, , Title, Error */ },
  props: ['value'],
  data: () => ({
    ticket: null,
    loading: false,
    error: false,
    ticketInfoDialog: false
  }),

  async mounted () {
    await this.loadTicket();
    this.applyTicketData();
    this.value.tick = {
      filter: 'td',
      callback: () => this.ticket.update(this.api)
    };
  },

  methods: {
    async loadTicket () {
      this.loading = true
      this.ticket = await Ticket.load(this.api, this.value.query.ticketId, this.value.query.elId)
        .catch(v => {
          this.error = v
          this.value.saved = null
          throw v;
          // return new Ticket({ ticketId: this.value.ticketId, elId: this.value.elId })
        })

      this.loading = false

      // Заменяем апдейт тикета
      const originalUpdate = this.ticket.update.bind(this.ticket)
      this.ticket.update = (api, data) => {
        this.loading = true
        return originalUpdate(api, data)
          .catch(v => {
            this.loading = false
            this.error = v
            this.value.saved = null
            throw v
          })
          .then(() => {
            this.loading = false
            this.error = false
            this.applyTicketData()
          });
      }
    },

    applyTicketData () {
      this.value.title = `#${this.value.query.ticketId}`
      if (!this.ticket) {
        return;
      }

      this.value.fullTitle = `#${this.ticket.ticketId} ${this.ticket.title}`
      this.value.color = this.ticket.isBlockedByMe() && 'blue'
    }
  }
}
</script>
