<template>
<a v-if="data.type === 'url'" @click="() => platform.openUrl(data.body)" href="#">{{data.body}}</a>
<IpParser v-else-if="data.type === 'ip'" :value="data.body"/>
<span v-else :style="`white-space: pre-wrap`">{{data.body}}</span>
</template>

<script>
import IpParser from '@/render/components/Ticket/Parsers/IpParser'

export default {
  components: { IpParser },
  props: ['data', 'type']
}
</script>
