<template>
<div>
  <v-text-field
    :loading="loading"
    style="min-width: 512px"
    dense
    hide-details
    v-if="editMode"
    v-model="ticketName"
    @keyup.enter="apply"
    @keyup.escape="editMode = false"
  />
  <v-btn text v-else @click="openEditor">{{ticket.title}}</v-btn>
</div>
</template>

<script>
import Ticket from '@/common/ticket.js';

export default {
  props: ['ticket'],
  data: () => ({
    editMode: false,
    ticketName: '',
    loading: false,
    rawTitles: [
      'Удаление сервера',
      'Активация сервера',
      'Зависла VM',
      'Установка софта',
      'Не работает интернет',
      'Выбор тарифа'
    ]
  }),

  methods: {
    openEditor () {
      if (!this.ticket.isBlockedByMe()) {
        alert('Запрос должен быть заблокирован Вами')
        return
      }

      this.ticketName = this.ticket.title
      this.editMode = true
    },

    async apply () {
      this.loading = true
      await this.ticket.update(this.api, await Ticket.edit(this.api, {
        name: this.ticketName,
        sok: 'ok'
      }, this.ticket.ticketId, this.ticket.elId))
        .catch(v => {
          alert(v.message)
        })

      this.loading = false
      this.editMode = false
    }
  },

  computed: {
    titles () {
      return this.rawTitles.map(v => `[${v} #${this.ticket.clientInformation.name}]`)
    }
  }
}
</script>
