export default class Ticket {
  constructor(options) {
    this.title = 'Название запроса';
    this.client = 'Name (samplename@domain.com)';
    this.ticketId = 0;
    this.elId = 0;
    this.createdAt = 0;
    this.location = 'nolocation';
    this.messages = [];

    Object.assign(this, options);
  }

  static parseWarning(warning) {
    const parsed = warning && warning.match(/<font color=".+">(.+)<\/font>/);
    return parsed && parsed[1];
  }

  static fromRaw(raw) {
    return new Ticket({
      title: raw.name,
      client: raw.client,
      ticketId: raw.ticket,
      elId: raw.elid || raw.id,
      blockedBy: raw.blocked_by || Ticket.parseWarning(raw.blocked_warning) || null,
      blockedByMe: raw.blocked_by_me || null,
      timeBlock: raw.time_block || null,
      unread: raw.unread === 'on',
      createdAt: Math.floor(new Date(raw.date_start).getTime() / 1000),
      location: raw.location,
      messages: raw.mlist && Ticket.parseMessages(raw.mlist),
      information: raw.mlist && Ticket.parseInformation(raw.mlist),
      clientInformation: Ticket.parseClientInformation(raw),
      attachedServiceId: raw.item,
      raw
    });
  }

  static parseInformation(mlist) {
    const getArr = v => (Array.isArray(v) ? v : [v]);
    return Object.fromEntries(
      getArr(mlist.message[0].rowgroup).map(
        v => [
          v.attr['@_name'],
          Object.fromEntries(getArr(v.row).map(b => [b.attr['@_name'], b['#text']]))
        ]
      )
    );
  }

  static parseClientInformation(raw) {
    if (!raw.mlist) {
      return { title: raw.client };
    }

    const clientMessage = raw.mlist.message.find(v => v.user && !v.user.position && v.user.realname !== 'System bot');
    if (!clientMessage) {
      return { title: raw.client };
    }

    const emailSrc = raw.client || clientMessage.title;
    // eslint-disable-next-line no-unused-vars
    const [_, email] = emailSrc.match(/\((.+@.+\..+)\)/) || emailSrc.match(/\((gdpr_deactivated)\)/);
    return {
      email,
      avatar: clientMessage.avatar && `https://my.msk.host${clientMessage.avatar['#text']}`,
      name: clientMessage.user && clientMessage.user.realname
    };
  }

  static parseMessages(mlist) {
    const parseFiles = msg => {
      const rawFiles = msg.file ? (Array.isArray(msg.file) ? msg.file : [msg.file]) : [];
      return rawFiles.map(v => ({
        name: v.name,
        elId: v.param['#text'],
        getUrl: api => `https://my.msk.host/billmgr?func=ticket.file&elid=${v.param['#text']}&auth=${api.token}`
      }));
    };

    return [...mlist.message].splice(1).map(v => ({
      type: v.attr['@_type'],
      title: v.user ? v.user.realname : v.title,
      body: v.body.toString(),
      avatar: v.avatar && `https://my.msk.host${v.avatar['#text']}`,
      isMine: v.user && !!v.user.position,
      files: parseFiles(v)
    }));
  }

  static async load(api, ticketId, elId) {
    return Ticket.fromRaw({
      ...await Ticket.edit(api, {}, ticketId, elId),
      ticket: ticketId,
      elid: elId
    });
  }

  static edit(api, params, ticketId, elId) {
    return elId && (+elId !== +ticketId)
      ? api.func('ticket.edit', { ...params, elid: elId })
      : api.func('ticket_all.edit', { ...params, elid: ticketId });
  }

  getId() {
    return `${this.ticketId}`;
  }

  getBorder() {
    if (this.unread) {
      return '2px solid #ff9c2a';
    }

    if (this.blockedByMe === 'on') {
      return '2px solid #2aabff';
    }

    if (this.blockedBy) {
      return '2px solid #7b0202';
    }

    return '0px';
  }

  isBlockedByMe() {
    return (this.timeBlock || this.blockedByMe) && !this.blockedBy;
  }

  isBlocked() {
    return this.blockedBy || this.isBlockedByMe();
  }

  async update(api, newObject) {
    const newCock = newObject && (newObject.doc || newObject);
    const object = (newCock && newCock.mlist) ? Ticket.fromRaw(newCock) : await Ticket.load(api, this.ticketId, this.elId);
    Object.assign(this, Object.fromEntries(Object.entries(object).filter(v => typeof v[1] !== 'undefined')));
  }

  async lock(api) {
    return this.update(api, await Ticket.edit(api, {
      sok: 'ok',
      clicked_button: 'ok_get'
    }, this.ticketId, this.elId));
  }

  async unlock(api) {
    await api.func('ticket.unblock', { elid: this.elId });
    return this.update(api);
  }

  async send(api, data, close = false) {
    const response = await Ticket.edit(api, {
      sok: 'ok',
      clicked_button: close ? 'ok' : undefined,
      ...data
    }, this.ticketId, this.elId);

    return close ? response : this.update(api, response);
  }
}
