<template>
<MyDialog title="Передать тикет в отдел" :loading="!items">
  <template v-slot:activator="{ on, attrs }">
          <v-btn icon v-bind="attrs" v-on="on"><v-icon>mdi-account-arrow-right</v-icon></v-btn>
        </template>
  <v-container>
    <v-select
      v-model="selected"
      :items="items && items.map(v => v['#text'])"
      label="Выберите отдел"
    ></v-select>
    <v-btn @click="transfer" color="primary" :loading="loading">Передать</v-btn>
  </v-container>
</MyDialog>
</template>

<script>
import MyDialog from '@/render/components/Common/MyDialog';

export default {
  components: { MyDialog },
  props: ['ticket'],
  data: () => ({
    items: null,
    loading: false,
    selected: 0
  }),

  async mounted () {
    const response = await this.api.func('support_tool_responsible', { plid: this.ticket.ticketId, elid: this.ticket.elId })
    this.items = response.tlist.val ? (Array.isArray(response.tlist.val) ? response.tlist.val : [response.tlist.val]) : []
  },

  methods: {
    async transfer () {
      this.loading = true
      await this.api.func('support_tool_responsible', {
        plid: this.ticket.ticketId,
        elid: this.ticket.elId,
        set_responsible: this.items.find(v => v['#text'] === this.selected).attr['@_key'],
        set_responsible_default: 'on',
        clicked_button: 'ok',
        sok: 'ok'
      }).then(() => this.ticket.send(this.api, {}, true)).catch(v => alert(v))

      alert('Успешно')
      this.loading = false
    }
  }
}
</script>
