<template>
<MyDialog v-model="dialog" :loading="!data">
  <template v-slot:activator="{ on, attrs }">
    <v-badge
      :color="filter.length > 0 && filtered.length > 0 ? 'accent' : 'rgba(0,0,0,0)'"
      overlap
      dot
    ><v-btn icon v-bind="attrs" v-on="on"><v-icon color="grey lighten-1">mdi-clipboard-list</v-icon></v-btn></v-badge>
  </template>
  <template v-slot:title>
    <v-text-field name="Фильтр" v-model="filter" autofocus/>
  </template>
  <v-list dense style="width: 100%; overflow-x: hidden !important" v-if="data">
      <v-list-item v-for="item in filtered" :key="item.id" two-line @click="() => select(item.msg)">
        <v-list-item-content>
          <v-list-item-title>{{item.name}}</v-list-item-title>
          <v-list-item-subtitle>{{item.msg}}</v-list-item-subtitle>
        </v-list-item-content>
      </v-list-item>
    </v-list>
</MyDialog>
</template>

<script>
import MyDialog from '@/render/components/Common/MyDialog';

export default {
  components: { MyDialog },
  props: ['ticket', 'text'],
  data: () => ({
    filter: '',
    dialog: false
  }),

  methods: {
    select(msg) {
      this.$emit('select', msg);
      this.dialog = false;
    }
  },

  watch: {
    text() {
      this.filter = this.text.replace(/Здравствуйте, /g, '');
    }
  },

  computed: {
    filtered() {
      return this.data && this.data.filter(v => v.name.toLowerCase().includes(this.filter.toLowerCase()));
    }
  },

  asyncComputed: {
    data() {
      return this.tkb.query('get', `billing/anstempl/${this.ticket.ticketId}/${this.ticket.elId}`);
      // return this.tkb.func('util.getAnstempl', { elId: this.ticket.elId, ticketId: this.ticket.ticketId });
    }
  }
};
</script>
