<template>
<MyDialog width="700" scrollable v-if="service" :title="service.name">
  <template v-slot:activator="{ on, attrs }">
    <v-list-item three-line v-bind="attrs" v-on="on">
      <v-list-item-avatar>
        <v-badge
          color="accent"
          overlap
          :content="['', '💳', '✔️', '⛔', '🗑️', '💫'][service.status]"
        ><v-icon>mdi-{{icon}}</v-icon></v-badge>
      </v-list-item-avatar>
      <v-list-item-content>
        <v-list-item-title>#{{service.id}}</v-list-item-title>
        <v-list-item-subtitle>{{service.cost}}₽ / {{ {
              [-50]: 'Д',
              [1]: 'М',
              [3]: '3М',
              [6]: '6М',
              [12]: 'Г',
              [0]: 'В'
            }[service.costperiod] }} | {{service.name}}</v-list-item-subtitle>
        <v-list-item-subtitle>
          <IpParser v-if="mainInfo.match(/(?:(?:25[0-5]|2[0-4][0-9]|[01]?[0-9][0-9]?)(\.|$)){4}/)" :value="service.ip"/>
          <div v-else>{{mainInfo}}</div>
        </v-list-item-subtitle>
      </v-list-item-content>
    </v-list-item>
  </template>
  <v-list nav dense>
      <v-list-item @click="() => tabMgr.open('Service', { elId: service.id })">
        <v-list-item-icon>
          <v-icon>mdi-link</v-icon>
        </v-list-item-icon>
        <v-list-item-title>Открыть</v-list-item-title>
      </v-list-item>
    </v-list>
    <Actions :service="service" :type="type"/>
</MyDialog>
<v-skeleton-loader v-else type="list-item-avatar-three-line"/>
</template>

<script>
import MyDialog from '@/render/components/Common/MyDialog';
import IpParser from '@/render/components/Ticket/Parsers/IpParser';
import Actions from '@/render/components/Service/Actions.vue';

export default {
  components: { MyDialog, IpParser, Actions },
  props: ['ticket'],
  data: () => ({
    type: null,
    service: null
  }),

  async mounted() {
    this.service = await this.tkb.query('get', `billing/service/${this.ticket.attachedServiceId}`)
      .catch(v => ({
        id: this.ticket.attachedServiceId,
        name: '',
        error: v.message,
        base: true
      }));
    
    this.type = this.service.type;
  },

  computed: {
    icon() {
      return {
        vds: 'cloud',
        domain: 'access-point',
        dnshost: 'form-select',
        vhost: 'web',
        dedic: 'desktop-classic'
      }[this.type] || 'help';
    },

    mainInfo() {
      if (this.service.base) {
        return this.service.error;
      }

      return this.service.ip;
    }
  }
};
</script>
