<template>
<div v-if="data">
  <TicketDetail :ticket="ticket" :data="data"/>
  <v-divider></v-divider>
  <v-list dense>
  <v-list-item @click="reload">
    <v-list-item-icon><v-icon>mdi-reload</v-icon></v-list-item-icon>
    <v-list-item-title>Обновить</v-list-item-title>
  </v-list-item>
    <v-list-group style="width: 100%; overflow-x: hidden">
      <template v-slot:activator>
        <v-list-item-icon><v-icon>mdi-wallet</v-icon></v-list-item-icon>
        <v-list-item-title>Счета</v-list-item-title>
      </template>
      <v-list dense style="width: 100%; overflow-x: hidden !important" >
        <v-list-item v-for="item in balanceList" :key="item.subaccount_id" :value="item" two-line>
          <v-list-item-content>
            <v-list-item-title>{{item.subaccount_balance}}</v-list-item-title>
            <v-list-item-subtitle>{{item.subaccount_project}} #{{ item.subaccount_id }} ({{ subaccountActive[item.subaccount_active] || item.subaccount_active }})</v-list-item-subtitle>
          </v-list-item-content>
        </v-list-item>
      </v-list>
    </v-list-group>
    <v-list-group style="width: 100%; overflow-x: hidden">
      <template v-slot:activator>
        <v-list-item-icon><v-icon>mdi-cloud</v-icon></v-list-item-icon>
        <v-list-item-title>Услуги</v-list-item-title>
      </template>
      <v-list dense style="width: 100%; overflow-x: hidden !important" >
        <v-list-item
          v-for="item in serviceList"
          :key="item.service_item"
          :value="item"
          two-line
          @click="() => tabMgr.open('Service', { elId: item.service_item })"
        >
          <v-list-item-content>
            <v-list-item-title>{{item.service_name['#text'] || item.service_name}}</v-list-item-title>
            <v-list-item-subtitle>{{item.service_location}} [{{item.service_status['#text'] || item.service_status}}]</v-list-item-subtitle>
          </v-list-item-content>
        </v-list-item>
      </v-list>
    </v-list-group>
  </v-list>
</div>
</template>

<script>
import TicketDetail from '@/render/components/Ticket/TicketDetail.vue'

export default {
  components: { TicketDetail },
  props: ['ticket'],
  data: () => ({
    data: null,
    subaccountActive: {
      '/manimg/common/img/p-on.png': 'акт.'
    }
  }),

  async mounted () {
    this.reload()
  },

  methods: {
    async reload () {
      this.data = null
      this.data = await this.api.func('support_tool_info', {
        plid: this.ticket.ticketId,
        elid: this.ticket.elId
      })
    }
  },

  computed: {
    balanceList () {
      const data = this.data.list[0].elem
      if (!data) {
        return []
      }

      return Array.isArray(data) ? data : [data]
    },
    serviceList () {
      const data = this.data.list[1].elem
      if (!data) {
        return []
      }

      return Array.isArray(data) ? data : [data]
    }
  }
}
</script>
