<template>
<div v-if="ticket" class="workshape" style="width: 100%; height: 100%">
  <TitleBar :ticket="ticket"/>
  <div class="messages">
    <v-row>
      <Message v-for="value in messages" :key="value.id" :value="value"/>
    </v-row>
  </div>
</div>
</template>

<style scoped>
.messages {
  position: absolute;
  top: 48px;
  bottom: 0px;
  overflow-y: scroll;
  right: 0px;
  left: 0px;
  padding-right: 5%;
  padding-left: 5%;
  padding-top: 20px;
}
</style>

<script>
import TitleBar from '@/render/components/Ticket/TitleBar.vue'
import Message from '@/render/components/Ticket/Message.vue'

export default {
  components: { TitleBar, Message },
  props: ['ticket'],

  computed: {
    messages () {
      if (this.ticket.messages.length === 0) {
        return []
      }

      const getMessage = message => ({
        body: message.body,
        files: message.files,
        type: message.type
      })

      const getState = message => ({
        title: message.title,
        avatar: message.avatar,
        isMine: message.isMine,
        parts: []
      })

      let currentState = getState(this.ticket.messages[0])
      const states = []
      this.ticket.messages.forEach(v => {
        if (currentState.title !== v.title || currentState.isMine !== v.isMine) {
          states.push(currentState)
          currentState = getState(v)
        }

        currentState.parts.push(getMessage(v))
      })

      states.push(currentState)
      return states
    }
  }
}
</script>
