<template>
<div style="width: 100%; height: 100%">
<v-textarea
  :dense="core.mobile"
  v-model="message"
  placeholder="Ваш ответ"
  autofocus
  auto-grow
  rows="1"
  :hide-details="core.mobile"
  :disabled="loading"
  @keyup.shift.enter.prevent="sendMessage"
  @keyup.ctrl.enter.prevent="okMessage"
  class="textarea"
>
  <template v-slot:prepend-inner>
    <Anstempl :ticket="ticket" :text="message" @select="msg => message += msg"/>
  </template>
  <template v-slot:append>
    <v-btn :loading="loading" icon @click="okMessage" v-if="message.length === 0"><v-icon>mdi-close</v-icon></v-btn>
    <v-btn :loading="loading" icon v-longclick="okMessage" @click="sendMessage" v-else color="primary"><v-icon>mdi-send</v-icon></v-btn>
  </template>
</v-textarea>
</div>
</template>

<style scoped>
.textarea {
  position: relative;
  width: 100%;
  height: 100%;
}
</style>

<script>
import Anstempl from '@/render/components/Ticket/Anstempl.vue';

export default {
  components: { Anstempl },
  props: ['ticket'],
  data: () => ({
    message: '',
    loading: false
  }),

  mounted() {
    if (this.ticket.messages.length === 1) {
      this.message = 'Здравствуйте, ';
    }

    if (this.tabMgr.get().saved && this.tabMgr.get().saved.areaText) {
      this.message = this.tabMgr.get().saved.areaText;
    }
  },

  methods: {
    async sendMessage(e) {
      e.preventDefault();
      this.loading = true;
      await this.ticket.send(this.api, { message: this.message });
      this.loading = false;
      this.message = '';
      this.tkb.query('put', 'staff/shift', { isActive: true });
    },

    async okMessage(e) {
      if (e) {
        e.preventDefault();
      }

      this.loading = true;
      await this.ticket.send(this.api, { message: this.message }, true);
      this.loading = false;
      this.tabMgr.close();
      this.tkb.query('put', 'staff/shift', { isActive: true });
    }
  },

  watch: {
    message() {
      if (this.tabMgr.get().saved) {
        this.tabMgr.get().saved.areaText = this.message;
      }
    }
  }
};
</script>
