<template>
<MyDialog >
  <template v-slot:activator="{ on, attrs }">
    <v-list-item two-line v-bind="attrs" v-on="on">
      <v-list-item-avatar color="grey"><img :src="ticket.clientInformation.avatar"></v-list-item-avatar>
      <v-list-item-content>
        <v-list-item-title>{{ticket.clientInformation.name}}</v-list-item-title>
        <v-list-item-subtitle>{{ticket.clientInformation.email}}</v-list-item-subtitle>
      </v-list-item-content>
    </v-list-item>
  </template>
  <template v-slot:title>
    <a class="a-title" @click="() => tabMgr.open('Account', { elId: data.customer_id })">{{ data.customer_name }}</a>
  </template>
  <v-simple-table dense>
      <template v-slot:default>
        <tbody>
          <tr><td>ID</td><td>{{ data.customer_id }}</td></tr>
          <tr><td>Наименование</td><td>{{ data.customer_name }}</td></tr>
          <tr><td>Дата регистрации</td><td>{{ data.customer_regdate }}</td></tr>
          <tr><td>Страна</td><td>{{ data.customer_country }}</td></tr>
          <tr><td>Email</td><td>{{ data.user_email }}</td></tr>
        </tbody>
      </template>
    </v-simple-table>
    <v-divider/>
    <br><span style="margin: 15px">Счета</span>
    <v-simple-table dense>
      <template v-slot:default>
        <thead>
          <tr><th>ID</th><th>Проект</th><th>Баланс</th><th>Состояние</th><th>Действие</th></tr>
        </thead>
        <tbody>
          <tr v-for="item in balanceList" :key="item.subaccount_id" :value="item">
            <td>{{ item.subaccount_id }}</td>
            <td>{{ item.subaccount_project }}</td>
            <td>{{ item.subaccount_balance }}</td>
            <td><img :src="`https://my.msk.host${ item.subaccount_active }`"/></td>
            <td>
                <PayIn :userId="data.customer_id">
                  <template v-slot:activator="{ on, attrs }">
                  <v-btn icon v-bind="attrs" v-on="on"><v-icon>mdi-plus</v-icon></v-btn>
                </template>
                </PayIn>
            </td>
          </tr>
        </tbody>
      </template>
    </v-simple-table>
    <v-divider/>
    <br><span style="margin: 15px">Услуги</span>
    <v-simple-table dense>
      <template v-slot:default>
        <thead>
          <tr><th>ID</th><th>Имя</th><th>Локация</th><th>Состояние</th><th>Действие</th></tr>
        </thead>
        <tbody>
          <tr v-for="item in serviceList" :key="item.service_item" :value="item">
            <td>{{ item.service_item }}</td>
            <td :color="item.service_name.attr ? item.service_name.attr['@_color'] : ''">{{ item.service_name['#text'] || item.service_name }}</td>
            <td>{{ item.service_location }}</td>
            <td :color="item.service_status.attr ? item.service_status.attr['@_color'] : ''">{{ item.service_status['#text'] || item.service_status }}</td>
            <td>
              <v-btn
                icon
                @click="tabMgr.open('Service', { elId: item.service_item })"
              ><v-icon>mdi-link</v-icon></v-btn>
            </td>
          </tr>
        </tbody>
      </template>
    </v-simple-table>
</MyDialog>
</template>

<script>
import MyDialog from '@/render/components/Common/MyDialog';
import PayIn from '@/render/components/User/PayIn.vue';

export default {
  components: { PayIn, MyDialog },
  props: ['data', 'ticket'],

  mounted() {
    this.avatarCache.setAvatar(this.data.customer_name, this.ticket.clientInformation.avatar);
  },

  computed: {
    balanceList() {
      const data = this.data.list[0].elem;
      if (!data) {
        return [];
      }

      return Array.isArray(data) ? data : [data];
    },
    serviceList() {
      const data = this.data.list[1].elem;
      if (!data) {
        return [];
      }

      return Array.isArray(data) ? data : [data];
    }
  }
};
</script>
