<template>
<div v-if="ticket.information">
  <RightSidebarService v-if="ticket.attachedServiceId" :ticket="ticket"/>
  <v-list dense>
  <TicketInfoPart v-for="table in tables" :key="table[0]" :value="table"/>
  </v-list>
</div>
</template>

<script>
import RightSidebarService from '@/render/components/Ticket/RightSidebarService.vue'
import TicketInfoPart from '@/render/components/Ticket/TicketInfoPart.vue'

export default {
  components: { RightSidebarService, TicketInfoPart },
  props: ['ticket'],
  computed: {
    tables () {
      return Object.entries(this.ticket.information)
    }
  }
}
</script>
