<template>
<div :style="style">
  <span v-if="isRu" style="white-space: pre-wrap;">
    <MessageToken v-for="(token, index) in tokenized" :key="index" :data="token" :type="value.type"/>
  </span>
  <v-tooltip bottom v-else>
    <template v-slot:activator="{ on, attrs }">
      <span v-bind="attrs" v-on="on"><MessageToken v-for="(token, index) in tokenized" :key="index" :data="token" :type="value.type"/></span>
    </template>
    <span>{{translated}}</span>
  </v-tooltip>
<br v-if="value.files.length > 0">
<div v-for="file in value.files" :key="file.name" :file="file">
  <MyDialog v-if="file.name.match(/\.(?:png|jpg|jpeg|bmp|gif)$/i)" :title="file.name">
    <template v-slot:activator="{ on, attrs }">
      <v-btn v-bind="attrs" v-on="on">
        <v-icon>mdi-file</v-icon>
        {{file.name}}
      </v-btn>
    </template>
    <img :src="file.getUrl(api)">
  </MyDialog>
  <v-btn
    v-else
    text
    dense
    @click="() => platform.openUrl(file.getUrl(api))"
  >
    <v-icon>mdi-file</v-icon>
    {{file.name}}
  </v-btn>
</div>

<br v-if="!last">
<br v-if="!last">
</div>
</template>

<script>
import MyDialog from '@/render/components/Common/MyDialog';
import translate from 'translate';
import MessageToken from '@/render/components/Ticket/MessageToken.vue';

translate.engine = 'libre';

export default {
  components: { MessageToken, MyDialog },
  props: ['value', 'last'],
  data: () => ({
    translating: false,
    translateResult: null
  }),
  computed: {
    isRu() {
      return true;
      // const body = this.value.body || 'а'
      // return (body.match(/[а-яА-Я]/g) || { length: 0 }).length / body.length > 0.3
    },

    translated() {
      if (!this.translating) {
        // eslint-disable-next-line vue/no-async-in-computed-properties
        translate(this.value.body, 'ru').then(v => { this.translateResult = v; });
      }

      return this.translateResult || '...';
    },

    tokenized() {
      const tokenTypes = [
        {
          slug: 'url',
          // eslint-disable-next-line no-useless-escape
          regexp: /(?:https|http|ftp):\/\/(www\.)?[-a-zA-Z0-9@:%._\+~#=]{1,256}\.[a-zA-Z0-9]{1,6}\b(?:[-a-zA-Z0-9@:%_\+.~#?&//=]*)/
        },
        {
          slug: 'ip',
          // eslint-disable-next-line no-useless-escape
          regexp: /(?:(?:25[0-5]|2[0-4][0-9]|[01]?[0-9][0-9]?)\.){3}(?:25[0-5]|2[0-4][0-9]|[01]?[0-9][0-9]?)/
          // ((25[0-5]|2[0-4][0-9]|[01]?[0-9][0-9]?)(\.|$)){4}
        }
      ];

      const findPart = str => {
        for (let i = 0; i < tokenTypes.length; i++) {
          const match = str.match(tokenTypes[i].regexp);
          if (match) {
            return { type: tokenTypes[i].slug, data: match };
          }
        }
      };

      const tokens = [];
      let str = this.value.body;
      while (str.length > 0) {
        const result = findPart(str);
        tokens.push({ body: result ? str.substring(0, result.data.index) : str, type: 'text' });
        if (result) {
          tokens.push({ body: result.data[0], type: result.type });
        }

        str = result ? str.substring(result.data.index + result.data[0].length) : '';
      }

      // console.log(str, tokens)
      return tokens;
    },

    style() {
      return this.value.type === 'inner' ? 'background: rgba(0,0,0,0.3); padding: 5px' : '';
    }
  }
};
</script>
